@import "@/style/common/common.scss";
















































































































































































































































































































.drag-area-size-droplist {
  margin: 0px auto;
  display: flex;
  justify-content: center;
  > div {
    padding: 0px 3px;
  }
  .ant-dropdown-link {
    white-space: nowrap;
  }
  .symbol-multiply-by {
    opacity: 0.6;
  }
}
//
.dropdown-option-item {
  padding-left: 20px;
  padding-right: 20px;
  display: inline-block;
  position: relative;
  .anticon {
    display: none;
    position: absolute;
    left: 0;
    top: 3px;
    &.anticon-delete {
      display: inline-block;
      position: relative;
      left: initial;
      top: initial;
      margin-left: 10px;
      opacity: 0.4;
      transition: all linear 0.2s;
      &:hover {
        opacity: 0.6;
      }
    }
  }
  &.active {
    .anticon {
      display: inline-block;
    }
  }
}
