//
@each $themename,$theme in $platformDevTheme {
    .develop_theme-#{$themename}{
        .idmcore_develop{
            
        .drag_simulator_overlay{
            .simulator_outline{
                &.simulator_dropping_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                }
                &.simulator_selected_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                }
                &.simulator_hovering_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                    background-color: map-get($map: $theme, $key: "lowAlphaColor");
                }
                .simulator_outline_node_selector{
                    background: map-get($map: $theme, $key: "mainColor");
                }
                .simulator_outline_actions{
                    &>div{
                        background: map-get($map: $theme, $key: "mainColor");
                    }
                }
            }
            .simulator_insertion{
                background-color:map-get($map: $theme, $key: "mainColor");
            }
            .simulator_insertion:after,
            .simulator_insertion:before{
                border-color:transparent transparent transparent map-get($map: $theme, $key: "mainColor")
            }
            .simulator_insertion:after{
                border-color:transparent map-get($map: $theme, $key: "mainColor") transparent transparent
            }
            .simulator_insertion.simulator_vertical:before{
                border-color:map-get($map: $theme, $key: "mainColor") transparent transparent
            }
            .simulator_insertion.simulator_vertical:after{
                border-color:transparent transparent map-get($map: $theme, $key: "mainColor")
            }
        }
        background-color: map-get($map: $theme, $key: "bgcolor");
        .rul_corner,.rul_ruler{
            background-color: map-get($map: $theme, $key: "qBgcolor");
        }
        .rul_corner:hover{
            background-color: map-get($map: $theme, $key: "bgcolor");
        }
        .rul_line{
            border-bottom-color: map-get($map: $theme, $key: "guidesColor");
            border-left-color: map-get($map: $theme, $key: "guidesColor");
            &.rul_line_dragged{
                // border-bottom: 1px dotted #ca0828;
                border-color: map-get($map: $theme, $key: "guidesColorD") !important;
            }
        }
        .rul_line:hover{
            border-bottom-color: map-get($map: $theme, $key: "guidesColorH");
            border-left-color: map-get($map: $theme, $key: "guidesColorH");
        }
        .top_toolbar{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 0px 3px 5px map-get($map: $theme, $key: "shadowColor");
            .ctrl_btn{
            background-color: map-get($map: $theme, $key: "transparentColor");
            }
            
            .idmcore_top_toolbar_box{
                border-top-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                background-color: map-get($map: $theme, $key: "qBgcolor");
                box-shadow: 0px 3px 5px map-get($map: $theme, $key: "shadowColor");
                
            
                .idm-hoverlayout-toolbar{
                  &>ul{
                      &>li{
                          &:hover:not(.module-toolbar-selected):not(.module-toolbar-disabled),&.active:not(.module-toolbar-selected):not(.module-toolbar-disabled){
                              background-color: map-get($map: $theme, $key: "mainColor");
                              svg{
                                color:map-get($map: $theme, $key: "qBgcolor") ;
                              }
                          }
                          &.module-toolbar-selected{
                              background-color: map-get($map: $theme, $key: "bgcolor");
                          }
                      }
                  }
                  .module-toolbar-icon{
                    color: map-get($map: $theme, $key: "minorFontColor");
                  }
              }
            }
        }
        .bottom_action_panel{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 0px -3px 5px map-get($map: $theme, $key: "shadowColor");
            .idm_bottom_action_panel_bottom_action{
              box-shadow: 0px -3px 5px map-get($map: $theme, $key: "shadowColor");
            }
            .dragarea-bottom-history{
                .history_date{
                  color: map-get($map: $theme, $key: "mainFontColor");
                }
                .history_action{
                  color: map-get($map: $theme, $key: "minorFontColor");
                }
                .history_content{
                  color: map-get($map: $theme, $key: "mainFontColor");
                }
                .history_info{
                  color: map-get($map: $theme, $key: "lowFontColor");
                }
                .idm-ant-timeline-item-tail{
                  border-left-color: map-get($map: $theme, $key: "lowBorderColor");
                }
            }
            .dragarea-bottom-datasource{
              .idm-dbd-tablebox{
                  .idm-ant-table-small{
                      border-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                  }
                  .idm-ant-table-fixed-header > .idm-ant-table-content > .idm-ant-table-scroll > .idm-ant-table-body{
                    background-color: map-get($map: $theme, $key: "qBgcolor");
                    &::-webkit-scrollbar-thumb {
                        background-color: map-get($map: $theme, $key: "scrollBarColor");
                    }
                  }
                  tr.idm-ant-table-expanded-row, tr.idm-ant-table-expanded-row:hover{
                    background: rgba(map-get($map: $theme, $key: "pureColor"),0.06);
                  }
                  .idm-ant-descriptions-item-content{
                    background-color: map-get($map: $theme, $key: "qBgcolor") !important;
                  }
                  .idm-ant-pagination-item-active{
                    background-color: map-get($map: $theme, $key: "bgcolor") !important;
                  }
                  .idm-ant-descriptions-bordered .idm-ant-descriptions-row,
                  .idm-ant-descriptions-bordered .idm-ant-descriptions-view,
                  .idm-ant-descriptions-bordered .idm-ant-descriptions-item-label, 
                  .idm-ant-descriptions-bordered .idm-ant-descriptions-item-content,
                  .idm-ant-table-tbody > tr > td,
                  .idm-ant-table-thead > tr > th,
                  .idm-ant-table-small > .idm-ant-table-content > .idm-ant-table-scroll > .idm-ant-table-header > table > .idm-ant-table-thead > tr{
                    border-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                  }
                  .idm-ant-table-tbody>tr.idm-ant-table-row-hover:not(.idm-ant-table-expanded-row):not(.idm-ant-table-row-selected)>td,
                  .idm-ant-table-tbody>tr:hover:not(.idm-ant-table-expanded-row):not(.idm-ant-table-row-selected)>td,
                  .idm-ant-table-thead>tr.idm-ant-table-row-hover:not(.idm-ant-table-expanded-row):not(.idm-ant-table-row-selected)>td,
                  .idm-ant-table-thead>tr:hover:not(.idm-ant-table-expanded-row):not(.idm-ant-table-row-selected)>td {
                      background-color: map-get($map: $theme, $key: "bgcolor") !important;
                  }
                  .idm-ant-pagination-disabled .idm-ant-pagination-item-link,
                  .idm-ant-table-thead > tr > th,td,.idm-ant-descriptions-bordered .idm-ant-descriptions-item-label{
                    color: map-get($map: $theme, $key: "minorFontColor");
                  }
              }
            }
        }
        .lr_sidebar{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 3px 5px 5px map-get($map: $theme, $key: "shadowColor");
            .ctrl_btn{
            background-color: map-get($map: $theme, $key: "transparentColor");
            }
            &.pos_right{
            box-shadow: -3px 5px 5px map-get($map: $theme, $key: "shadowColor");
            }
            .idm-ant-tabs{
            &.idm-ant-tabs-bottom .idm-ant-tabs-bottom-bar{
                background-color: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "minorFontColor");
            }
            }
            .pane_content_title{
            background-color: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "minorFontColor");
            }
        }
        //
        .drag_workspace{
            background-color: map-get($map: $theme, $key: "workspaceBgcolor");
            box-shadow: 0px 0px 10px map-get($map: $theme, $key: "shadowColor");
            .idm_module{
                &.idm_module_absolute{
                    &.idm_region_check{
                        outline-color: map-get($map: $theme, $key: "minor2Color");
                    }
                }
            }
        }
        //
        .mobile_status_bar{
          color: map-get($map: $theme, $key: "mainFontColor");
        }
        
        //
        .dragarea-top-left-title{
            >div.backbtn{
                color: map-get($map: $theme, $key: "minorFontColor");
            }
            .title-content{
                color: map-get($map: $theme, $key: "minorFontColor");
                .readonlytitle{
                    div{
                    }
                }
            }
        }
        .idmcore_topbar_left_box{
            .idmcore-topbar-hover-layer{
                .idmcore-icon{
                  color: map-get($map: $theme, $key: "lowFontColor");
                }
                &:hover{
                    background-color: map-get($map: $theme, $key: "bgcolor");
                }
                &.idmcore-active{
                  background-color: map-get($map: $theme, $key: "mainColor");
                  .idmcore-icon{
                    color: map-get($map: $theme, $key: "qBgcolor");
                  }
                }
            }
        }
        //
        .dragarea-top-right-action{
            &>div{
            a,i,.oneicon{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            &:hover{
                a,i,.oneicon{
                color: map-get($map: $theme, $key: "mainFontColor");
                }
            }
            &[disabled]{
                a,i,.oneicon{
                    color: map-get($map: $theme, $key: "lowFontColor");
                }
            }
            .savebtn{
                color: map-get($map: $theme, $key: "qBgcolor");
                background-color: map-get($map: $theme, $key: "mainColor");
            }
            }
        }
        //
        .drag-area-size-droplist{
            .symbol-multiply-by{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
        }
        }
        
        .scrollbar_style{
            &::-webkit-scrollbar-thumb {
                background-color: map-get($map: $theme, $key: "scrollBarColor");
            }
        }
        //
        .idm-quick-search{
          color: map-get($map: $theme, $key: "qBgcolor");
          background-color: map-get($map: $theme, $key: "mainColor");
          box-shadow: 0px 0px 15px map-get($map: $theme, $key: "showyShadowColor");
          .idm-quick-search-input{
              input{
                  &::placeholder{
                    color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                  }
              }
              .idm-quick-search-type{
                .idm-ant-radio-button-wrapper {
                    color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.9);
                    border: 1px solid rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                }
                .idm-ant-radio-button-wrapper:first-child{
                    border-left: 1px solid rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                }
                &.idm-ant-radio-group-solid .idm-ant-radio-button-wrapper-checked:not(.idm-ant-radio-button-wrapper-disabled){
                    
                    background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                }
                .idm-ant-radio-button-wrapper:not(:first-child)::before{
                    background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                }
            }
          }
          .idm-quick-search-check{
              color: map-get($map: $theme, $key: "qBgcolor");
              .idm-ant-checkbox-inner{
                  border: 1px solid rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
              }
              .idm-ant-checkbox-checked::after{
                  border: 1px solid rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
              }
              .idm-ant-checkbox + span{    
                  color: map-get($map: $theme, $key: "qBgcolor");
              }
              .idm-ant-checkbox-checked .idm-ant-checkbox-inner::after{
                border-color: map-get($map: $theme, $key: "qBgcolor");
              }
          }
          .idm-quick-search-close{
            color: map-get($map: $theme, $key: "qBgcolor");
          }
          .idm-quick-search-results{
              .idm-quick-search-result-item-wrapper{
                  .idm-quick-search-type-node{
                      background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.3);
                  }
                  .idm-quick-search-result-item-title-wrapper{
                      .idm-quick-search-result-item-name{
                        color: map-get($map: $theme, $key: "qBgcolor");
                      }
                      .idm-quick-search-result-item-version{
                        color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.3);
                      }
                  }
                  .idm-quick-search-result-item-content{
                      color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.6);
                  }
                  &:hover{
                      background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.1);
                  }
              }
              
          }
          .idm-svg-icon{
            color: map-get($map: $theme, $key: "qBgcolor");
          }
        }
          //
          .idm-quick-search-popover.min-search{
            color: map-get($map: $theme, $key: "mainFontColor");
            background-color: map-get($map: $theme, $key: "qBgcolor");
            .idm-quick-search-input{
                input{
                  border: 1px solid map-get($map: $theme, $key: "borderColor");
                    &::placeholder{
                      color: map-get($map: $theme, $key: "lowFontColor");
                    }
                }
            }
            .idm-quick-search-results{
                .idm-quick-search-result-item-wrapper{
                    .idm-quick-search-type-node{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                        .idm-svg-icon{
                          color: map-get($map: $theme, $key: "lowFontColor");
                        }
                    }
                    .idm-quick-search-result-item-title-wrapper{
                        .idm-quick-search-result-item-name{
                          color: map-get($map: $theme, $key: "mainFontColor");
                        }
                        .idm-quick-search-result-item-version{
                          color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                        }
                    }
                    .idm-quick-search-result-item-content{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                    }
                    &:hover{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                    }
                    .check_layer{
                        background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.7);
                        color: map-get($map: $theme, $key: "mainColor");
                    }
                }
                &.idm-attr-item-box{
                  .idm-attr-item-wrapper{
                    &:hover{
                        background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.1);
                    }
                    .check_layer{
                      background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.7);
                      border: 2px solid map-get($map: $theme, $key: "mainColor");
                      color: map-get($map: $theme, $key: "mainColor");
                    }
                  }
                }
            }
          }
          
        //
        .idm_drag_scale_control{
          color: map-get($map: $theme, $key: "mainFontColor");
          background-color: map-get($map: $theme, $key: "qBgcolor");
          box-shadow: 0px 3px 5px map-get($map: $theme, $key: "shadowColor");
          &>div{
              &:hover{
                background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.15);
              }
          }
      }
        //market no data
        .no_market_content{
            color: map-get($map: $theme, $key: "lowFontColor");
        }
        //
        .drag_container_empty{
            &:before{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            background: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "lowFontColor");
            outline-color: rgba(map-get($map: $theme, $key: "pureColor"),0.35);
        }
        .page-component{
            .pagecom_navigator{
                .pagecom_navigator_inner{
                    color: rgba(map-get($map: $theme, $key: "pureColor"),0.55);
                    .right_arrow{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.15);
                    }
                }
            }
            
            .idm-core-leaf-selected{
                >.idm-core-leaf-title {
                  background-color:rgba(map-get($map: $theme, $key: "pureColor"), 0.06);
                }
            }
            .idm-core-leaf-hovering{
                >.idm-core-leaf-title {
                  background-color:rgba(map-get($map: $theme, $key: "pureColor"), 0.03);
                }
            }
            .idm-core-leaf-title {
                color: map-get($map: $theme, $key: "mainFontColor");
                border-bottom-color:rgba(map-get($map: $theme, $key: "pureColor"), 0.1);
            }
        }
        //
        .mix-attribute{
            .mixattr_baseinfo{
                color: map-get($map: $theme, $key: "mainFontColor");
              .mixattr_baseinfo_version{
                color: map-get($map: $theme, $key: "lowFontColor");
              }
            }
        }
        //
        .mix-attribute,.data-attribute{
            .collapse_level_0 .idm-ant-collapse-item .idm-ant-collapse-content{
                background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                
                .collapse_level_1 .idm-ant-collapse-item .idm-ant-collapse-content{
                    background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                    
                    .collapse_level_2 .idm-ant-collapse-item .idm-ant-collapse-content{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                    }
                }
            }
            .sort-arrow-icon{
              color: rgba(map-get($map: $theme, $key: "pureColor"),0.5);
              &:hover{
                color: rgba(map-get($map: $theme, $key: "pureColor"),0.7);
              }
            }
            .label_text{
                color: map-get($map: $theme, $key: "minorFontColor");
            }
            //padding margin 
            .ibox_layout_padding_margin{
              .ibox_layout_handle{
                  &.ibox_handle_top{
                      border-top-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_bottom{
                      border-bottom-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_left{
                      border-left-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_right{
                      border-right-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
              }
              .ibox_inline_input{
                  .ibox_inline_input_text{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                  }
                  .ibox_inline_input_input{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                      background: rgba(map-get($map: $theme, $key: "pureColor"),0.5);
                      &::placeholder{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.2);
                      }
                  }
                  &.ibox_highlight{
                      .ibox_inline_input_text{
                          color: map-get($map: $theme, $key: "mainColor");
                      }
                  }
              }
              .box_auto_btn_box{
                  color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  &:hover{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.8);
                  }
                  &.active{
                      background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                  }
              }
          }
          .ctrl_box_border,.ctrl_box_radius{
              border: 1px solid map-get($map: $theme, $key: "lowBorderColor");
              .border_ctrl_wrap{
                  .border_setting_row{
                      &>div{
                          color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                          &:hover{
                              color: rgba(map-get($map: $theme, $key: "pureColor"),0.8);
                          }
                          &.active{
                              background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                          }
                      }
                  }
              }
          }
          .ctrl_box{
              .idm-svg-icon{
                color: map-get($map: $theme, $key: "mainColor");
              }
              .action_item{
                color: map-get($map: $theme, $key: "minorFontColor");
                &:hover{
                  background-color: map-get($map: $theme, $key: "lowAlphaColor");
                }
                  .action_icon>*{
                    color: map-get($map: $theme, $key: "lowFontColor");
                  }
              }
              &.idm-layout-flex{
                  .idm-layout-position-shortcut{
                      svg{
                          &.svg-active{
                              color: map-get($map: $theme, $key: "mainColor");
                          }
                      }
                  }
              }
          }
          .layoutType-block,.layoutType-inline{
            &.attr-variable-binding-root{
                .ctrl_box{
                  .variable-binding-box{
                    &.variable-active{
                      background-color: map-get($map: $theme, $key: "mainColor");
                      .variable-binding-icon{
                        color: map-get($map: $theme, $key: "qBgcolor")
                      }
                    }
                    &:not(.variable-active){
                      &:hover{
                        .variable-binding-icon{
                          color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                        }
                      }
                    }
                  }
                  .variable-binding-icon{
                    color: rgba(map-get($map: $theme, $key: "pureColor"),0.4);
                  }
                }
            }
          }
        }
        //dom
        .top_toolbar,.lr_sidebar,.bottom_action_panel,.idm_attr_actionselect,.idm_dev_popover,.idm_dev_modal{
          
          .monacoeditor-box{
            border-color: map-get($map: $theme, $key: "borderColor");
          }
          .monacoeditor-box-title{
            color: map-get($map: $theme, $key: "minorFontColor");
          }
          //ant input number
            .idm-ant-input-number{
                background:transparent;
                border-color: map-get($map: $theme, $key: "borderColor");
                color: map-get($map: $theme, $key: "minorFontColor");
                &:hover,&:focus,&.idm-ant-input-number-focused{
                border-color: map-get($map: $theme, $key: "minorColor");
                }
            }
            .idm-ant-input-number:hover {
            border-color: map-get($map: $theme, $key: "minorColor");
            }
            .idm-ant-input-number:focus,.idm-ant-input-number-focused {
            border-color: map-get($map: $theme, $key: "minorColor");
            box-shadow: 0 0 0 2px map-get($map: $theme, $key: "mainShadowColor");
            }
            .idm-ant-input-number-handler-up-inner, .idm-ant-input-number-handler-down-inner,.idm-ant-input-search-icon {
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            .idm-ant-input-number-handler:hover .idm-ant-input-number-handler-up-inner,
            .idm-ant-input-number-handler:hover .idm-ant-input-number-handler-down-inner {
            color: map-get($map: $theme, $key: "minorColor");
            }
