@import "@/style/common/common.scss";






















































































































































































.dragarea-top-left-title {
  display: flex;
  justify-content: start;
  > div.backbtn {
    cursor: pointer;
    width: 60px;
    text-align: center;
  }
  .version-dropdown {
    margin-left: 15px;
  }
  .updatebtn button {
    font-size: 12px;
  }
  .title-content {
    .readonlytitle {
      display: flex;
      div {
        display: inline-block;
        max-width: 170px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
}
