@import "@/style/common/common.scss";
































































































































































































































.idm-graphical-select {
    display: flex;

    .left-content {
        width: 320px;
        height: 360px;
        overflow: auto;
    }

    .right-content {
        width: 60px;
        text-align: center;
        color: #333;
        border-right: 2px solid #DDD;

        .graphical-type {
            height: 30px;
            line-height: 30px;
            cursor: pointer;

            &.active {
                background: #DDD;
            }

            &:hover {
                background: #DDD;
            }
        }

    }
}
