.idmcore_develop{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
    transition: all linear 0.3s;
    #drag_wrapper{
        height: 100vh;
        width: 100vw;
        position: relative;
        z-index: 100;
        transition: all linear 0.3s;
        overflow: auto;
        
        .drag_workspace{
            transition: background linear 0.3s;
        }
        //
        .mobile_status_bar{
            height: 24px;
            line-height: 24px;
            display: flex;
            justify-content: space-between;
            padding: 0px 8px;
            .mobile_bar_icon{
                margin-left: 8px;
            }
        }
        .idm_workspace_outbox{
            position: relative;
            .hover_module_region_box{
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 0;
                position: absolute;
                pointer-events: none;
            }
            &.idm_workspace_grab{
                cursor: grab;
                .idm_drag_documtnt{
                    pointer-events: none;
                }
            }
            &.idm_workspace_grabbing{
                cursor: grabbing;
            }
        }
    }
    .drag_simulator_overlay{
        pointer-events: none;
        overflow: visible;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 60;
        .simulator_outline{
            box-sizing: border-box;
            pointer-events: none;
            position: absolute;
            &.simulator_dropping_outline{
                outline: 1px solid rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -1px;
            }
            &.simulator_selected_outline{
                outline: 2px solid rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -2px;
            }
            &.simulator_hovering_outline{
                outline: 1px dashed rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -1px;
            }
            .simulator_selected_outline_actions_wrapper{
                position: absolute;
                display: flex;
                font-size: 12px !important;
            }
            .simulator_space_top_right{
                right: 0;
                top: -22px;
            }
            .simulator_space_bottom_right{
                right: 0;
                bottom: -22px;
            }
            .simulator_outline_node_selector{
                position: relative;
                color: #fff;
                border-radius: 3px;
                // margin-right: 2px;
                pointer-events: auto;
                flex-grow: 0;
                flex-shrink: 0;
                .simulator_outline_node_selector_current{
                    padding: 0 6px;
                    display: flex;
                    align-items: center;
                    height: 20px;
                    cursor: pointer;
                    transition: opacity linear 0.2s;
                    &:hover{
                        opacity: 0.8;
                    }
                }
                .idm-svg-icon{
                    margin-right: 5px;
                    flex-grow: 0;
                    flex-shrink: 0;
                    font-size: 14px !important;
                    max-height: 14px;
                    width: 14px;
                    fill: currentColor;
                    color: white;
                }
                .node_down{
                    transition: all .3s ease;
                }
                .node_text{
                    margin-right: 8px;
                    flex-grow: 0;
                    flex-shrink: 0;
                }
                .simulator_outline_node_selector_popup{
                    position: absolute;
                    left: 0;
                    right: 0;
                    opacity: 0;
                    visibility: hidden;
                    transition: all linear 0.2s;
                    &.simulator_outline_node_selector_popup_top{
                        bottom: 20px;
                    }
                    &.simulator_outline_node_selector_popup_bottom{
                        top: 20px;
                    }
                    .simulator_outline_node_selector_popup_item{
                        margin: 2px 0;
                        transition: opacity linear 0.2s;
                        cursor: pointer;
                        &:hover{
                            opacity: 0.8;
                        }
                        .simulator_outline_node_selector_popup_item_inner{
                            background: #78869a;
                            display: inline-flex;
                            border-radius: 3px;
                            padding-left: 6px;
                            align-items: center;
                            height: 20px;
                            color: #fff;
                            cursor: pointer;
                            overflow: visible;
                        }
                        .node_text{
                            margin-right: 0;
                            line-height: 20px;
                            background: #78869a;
                            border-radius: 3px;
                            padding-right: 6px;
                        }
                    }
                }
                &:hover{
                    .simulator_outline_node_selector_popup{
                        opacity: 1;
                        visibility: visible;
                    }
                    .node_down{
                        transform: rotate(180deg);
                    }
                }
            }
            .simulator_outline_actions{
                pointer-events: auto;
                display: flex;
                &>div{
                    box-sizing: border-box;
                    cursor: pointer;
                    height: 20px;
                    width: 20px;
                    text-align: center;
                    color: #ffffff;
                    border-radius: 3px;
                    margin-left: 2px;
                    transition: opacity linear 0.2s;
                    line-height: 20px;
                    &:hover{
                        opacity: 0.8;
                    }
                }
                .idm_module_handle{
                    display: block;
                    width: 100%;
                    height: 100%;
                }
                .idm_module_handle_animate{ 
                    -webkit-animation-duration: 1s; 
                    animation-duration: 1s; 
                    -webkit-animation-fill-mode: forwards; 
                    animation-fill-mode: forwards ;
                    animation:twinkling 0.5s infinite ease-in-out;
                    background-color: red !important;
                } 
                @keyframes twinkling{ 
                    0%{ 
                        opacity: 0.1; 
                        margin-top: -3px;
                    } 
                    50%{ 
                        opacity: 1; 
                        margin-top: 0px;
                    } 
                    100%{ 
                        opacity: 0.1; 
                        margin-top: -3px;
                    } 
                }
                // background: var(--color-brand,#006cff);
                // border-radius: 3px;
                // box-shadow: 0 1px 4px 0 rgb(31 56 88 / 15%);
            }
        }
        .simulator_insertion{
            position:absolute;
            display:block;
            z-index:12;
            pointer-events:none!important;
            height:2px;
            line-height:0;
            font-size:0;
            will-change:transform
        }
        .simulator_insertion:after,
        .simulator_insertion:before{
            position:absolute;
            content:"";
            left:0;
            top:-2px;
            width:0;
            height:0;
            border-style:solid;
            border-width:3px 0 3px 5px;
        }
        .simulator_insertion:after{
            left:auto;
            right:0;
            border-width:3px 5px 3px 0;
        }
        .simulator_insertion.simulator_vertical{
            width:2px;
            height:auto
        }
        .simulator_insertion.simulator_vertical:after,
        .simulator_insertion.simulator_vertical:before{
            top:0;
            left:-2px;
            right:auto
        }
        .simulator_insertion.simulator_vertical:before{
            border-width:5px 3px 0;
        }
        .simulator_insertion.simulator_vertical:after{
            top:auto;
            bottom:0;
            border-width:0 3px 5px;
        }

    }
    //
    .idm-com-simulator-overlay,.idm-group-simulator-overlay{
        pointer-events: auto;
        .idm_simulator_line{
            top: 0;
            left: 0;
            width: 1px;
            position: absolute;
            box-sizing: border-box;
            pointer-events: none;
            background-color: #1890ff;
            &.isl-top{
                height: calc(var(--width) * 1px);
                transform: rotate(-90deg) scaleX(calc(1/var(--scale))) translateX(-50%);
                transform-origin: 0 0;
            }
            &.isl-bottom{
                height: calc(var(--width) * 1px);
                transform: translateY(calc(var(--height) * 1px)) rotate(-90deg) scaleX(calc(1/var(--scale))) translateX(-50%);
                transform-origin: 0 0;
            }
            &.isl-left{
                height: calc(var(--height) * 1px);
                transform: translateX(-50%) scaleX(calc(1/var(--scale)));
            }
            &.isl-right{
                height: calc(var(--height) * 1px);
                transform: translateX(calc(var(--width) * 1px)) translateX(-50%) scaleX(calc(1/var(--scale)));
            }
        }
        .idm_simulator_anchor{
            top: 0;
            left: 0;
            width: 10px;
            border: solid 1px #1890ff;
            height: 10px;
            position: absolute;
            // box-shadow: 0px 0px 0px 0.5px #656565;
            box-sizing: border-box;
            // border-radius: 100%;
            background-color: #fff;
            display: var(--hiddenAnchor);
            &.isa-north{
                cursor: ns-resize;
                transform: translateX(calc(var(--width)/2 * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-east{
                cursor: ew-resize;
                transform: translateX(calc(var(--width) * 1px)) translateY(calc(var(--height)/2 * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-south{
                cursor: ns-resize;
                transform: translateX(calc(var(--width)/2 * 1px)) translateY(calc(var(--height) * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-west{
                cursor: ew-resize;
                transform: translateY(calc(var(--height)/2 * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-north-west{
                cursor: nw-resize;
                transform: translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));            
            }
            &.isa-north-east{
                cursor: ne-resize;
                transform: translateX(calc(var(--width) * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-south-east{
                cursor: se-resize;
                transform: translateX(calc(var(--width) * 1px)) translateY(calc(var(--height) * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
            &.isa-south-west{
                cursor: sw-resize;
                transform: translateY(calc(var(--height) * 1px)) translateX(-50%) translateY(-50%) scale(calc(1/var(--scale)));
            }
        }
        &.idm-com-lockclass{
            .idm_simulator_line{
                opacity: 0.5;
            }
            .idm_simulator_anchor{
                border: none;
                display: flex;
                background: none;
                box-shadow: none;
                align-items: center;
                justify-content: center;
                cursor:default
            }
        }
    }
    //
    .idm-halfgroup-simulator-overlay{
        box-shadow: 0px 0px 0px calc(0.5/var(--scale) * 1px) #188fff46, 0px 0px 0px calc(0.5/var(--scale) * 1px) #188fff46 inset;
        pointer-events: none;
        width: calc(var(--width) * 1px);
        height: calc(var(--height) * 1px);
    }

    .top_toolbar{
        position:absolute;
        height: 50px;
        left: 0;
        right: 0;
        top: 0;
        z-index: 110;
        transition: all linear 0.3s;
        .toolbar_inner_box{
        }
        .ctrl_btn{
            position: absolute;
            width: 40px;
            height: 20px;
            color: #ffffff;
            font-size: 12px;
            background-color: rgba(0,0,0,0.4);
            text-align: center;
            left: 50%;
            margin-left: -20px;
            bottom: 80px;
            line-height: 20px;
            user-select: none;
            cursor: pointer;
            opacity: 0;
            transition: all linear 0.3s;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            &:hover{
                opacity: 1;
            }
        }
        .idmcore_topbar_left_box{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            .idmcore-topbar-hover-layer{
                margin-left: 50px;
                width: 30px;
                height: 30px;
                border-radius: 5px;
                text-align: center;
                line-height: 28px;
                cursor: pointer;
                .idmcore-icon{
                    font-size: 20px;
                    max-height: 20px;
                    width: 20px;
                    fill: currentColor;
                    vertical-align: middle;
                    color: #999999;
                }
                &:hover{
                    background-color: #F1F1F1;
                }
                &.idmcore-active{
                    background-color: #1890ff;
                    .idmcore-icon{
                        color: #FFFFFF;
                    }
                }
            }
        }
        .idmcore_top_toolbar_box{
            position: absolute;
            height: 0px;
            border-top-style: solid;
            border-top-width: 1px;
            overflow: hidden;
            transition: all linear 0.3s;
            padding: 0 10px;
            animation-duration: 0.3s;
            animation-timing-function: linear;
            &.has_anim{
                animation-name: ToolbarAnim-out;
                // animation-play-state: running;
                // animation-fill-mode: both;
                @keyframes ToolbarAnim-out {
                    0% {
                        height: 40px;
                    transform-origin: 0% 0%;
                    }
                    100% {
                    transform-origin: 0% 0%;
                    height: 0;
                    }
                }
            }
            &.show_active{
                animation-name: ToolbarAnim;
                height: 40px;
                // animation-play-state: running;
                // animation-fill-mode: both;
                @keyframes ToolbarAnim {
                    0% {
                        height: 0;
                    transform-origin: 0% 0%;
                    }
                    100% {
                    transform-origin: 0% 0%;
                    height: 40px;
                    }
                }
            }
            .idm-hoverlayout-toolbar{
                height: 100%;
                &>ul{
                    height: 40px;
                    padding: 0 !important;
                    margin: 0 !important;
                    display: flex;
                    flex-wrap: nowrap;
                    align-items: center;

                    &>li{
                        list-style: none;
                        text-align: center;
                        padding: 3px 0;
                        margin: 0 5px;
                        width: 29px;
                        cursor: pointer;
                        flex-shrink: 0;
                        border-radius: 4px;
                        &:hover:not(.module-toolbar-selected):not(.module-toolbar-disabled),&.active:not(.module-toolbar-selected):not(.module-toolbar-disabled){
                        }
                        &.module-toolbar-selected{
                        }
                        &.module-toolbar-disabled{
                            opacity: 0.3;
                            cursor:not-allowed;
                        }
                    }
                }
                .module-toolbar-icon{
                    outline: none;
                    font-size: 20px;
                    max-height: 20px;
                    width: 20px;
                    fill: currentColor;
                    &.size-26{
                        width: 26px;
                    }
                    &.size-22{
                        font-size: 22px;
                        max-height: 22px;
                        width: 22px;
                    }
                    &.size-18{
                        font-size: 18px;
                        max-height: 18px;
                        width: 18px;
                    }
                }
            }
        }
    }
