    .lr_sidebar{
        position:absolute;
        width: 300px;
        top: 50px;
        bottom: 0px;
        z-index: 111;
        transition: all linear 0.3s;
        .ctrl_btn{
            position: absolute;
            width: 20px;
            height: 40px;
            color: #ffffff;
            font-size: 12px;
            background-color: rgba(0,0,0,0.4);
            text-align: center;
            top: 50%;
            margin-top: -20px;
            line-height: 16px;
            padding-top: 4px;
            user-select: none;
            cursor: pointer;
            opacity: 0.8;
            transition: all linear 0.3s;
            &:hover{
                opacity: 1;
            }
        }
        &.pos_left{
