                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px;
            }
            .search_box_input{
                width: 228px;
            }
            .component_rectangle_info{
                width: 46%;
            }
            &.screen_type_402{
                width: 600px;
                .idm-ant-tabs .idm-ant-tabs-bottom-bar{
                    .idm-ant-tabs-nav .idm-ant-tabs-tab {
                        width: 300px !important;
                    }
                }
                .component_rectangle_info{
                    width: 23%;
                }
                &.area_close{
                    left: -600px;
                }
                .ctrl_btn{
                    right: 650px;
                }
                
                //
                .component_grid_info{
                    width: 16.6%;
                }
