                left: 350px;
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
            }
            &.screen_type_403{
                width: 600px;
                .idm-ant-tabs .idm-ant-tabs-bottom-bar{
                    .idm-ant-tabs-nav .idm-ant-tabs-tab {
                        width: 200px !important;
                    }
                }
                &.area_close{
                    right: -600px;
                }
                .ctrl_btn{
                    left: 650px;
                }
                
                .layoutType-inline{
                    .label_text{
                        width: 140px;
                    }
                }
            }
            .idm-ant-input-number{
                width: auto;
            }
