        .idm-ant-tabs{
            height: 100%;
            .idm-ant-tabs-content{
                height: calc(100% - 44px);
            }
            .idm-ant-tabs-bottom-bar{
                .idm-ant-tabs-nav .idm-ant-tabs-tab {
                    width: 100px;
                }
            }
            .idm-ant-tabs-nav .idm-ant-tabs-tab {
                text-align: center;
                margin: 0;
            }
            &.idm-ant-tabs-bottom .idm-ant-tabs-bottom-bar{
                margin-top: 0px;
                border-top: none;
                transition: background linear 0.3s;
            }
        }
        .pane_content_title{
            height: 40px;
            line-height: 40px;
            padding: 0px 15px;
            font-size: 16px;
            position: relative;
            transition: background linear 0.3s;
            .fewer_btn{
                position: absolute;
                right: 0;
                top: 0;
                width: 50px;
                text-align: center;
                opacity: 0.6;
                transition: all linear 0.3s;
                cursor: pointer;
                &:hover{
                opacity: 0.8;
                }
            }
            i.anticon{
                opacity: 0.4;
                cursor: pointer;
                margin-left: 5px;
                transition: all linear 0.3s;
                &:hover{
                opacity: 0.6;
                }
            }
        }
