    .mix-attribute,.data-attribute{
        height: calc(100% - 40px);
        overflow: auto;
        position: relative;
        .mixattr_baseinfo{
          padding:10px 15px;
          position: relative;
          .mixattr_baseinfo_version{
            display: flex;
            align-items: center;
            font-size: 12px;
          }
        }
        .mixattr_baseinfo,.mixattr_module_selected_box{
          .help_icon{
            position: absolute;
            right: 15px;
            top: 50%;
            margin-top: -9px;
            font-size: 18px;
            opacity: 0.4;
            transition: all linear 0.2s;
            cursor: pointer;
            &:hover{
            opacity: 0.5;
            }
            &.right-40{
                right: 40px;
                outline: none;
            }
          }
        }
        .idm-ant-divider-horizontal{
            margin: 0px;
        }
        .mixattr_attribute_box.pt10{
            padding-top: 10px;
        }
        .idm-ant-collapse-icon-position-right > .idm-ant-collapse-item > .idm-ant-collapse-header{
            padding: 6px 15px;
            font-weight: 600;
            .idm-ant-collapse-arrow{
                right: 15px;
            }
        }
        .idm-ant-collapse-content > .idm-ant-collapse-content-box{
            padding: 10px 15px;
        }
        .help_tip_icon{
            opacity: 0.4;
            transition: all linear 0.2s;
            &:hover{
            opacity: 0.5;
            }
        }
        .idm-ant-collapse{
            border-top: none;
        }
        .idm-ant-collapse-extra{
            position: absolute;
            right: 36px;
            top: 0px;
            line-height: 34px;
            .extra_op{
                i{
                    margin-left: 10px;
                }
            }
        }
        .layoutType-block,.layoutType-inline{
            margin-bottom: 10px;
            .variable-binding-box{
                display: none;
            }
            &.attr-variable-binding-root{
                .ctrl_box{
                    position: relative;
                    padding-right: 26px;
                    .variable-binding-box{
                        display: block;
                        position: absolute;
                        right: 0px;
                        top: 50%;
                        transform: translate(0%,-50%);
                        cursor: pointer;
                        width: 20px;
                        height: 20px;
                        border-radius: 3px;
                    }
                    .variable-binding-icon{
                        outline: none;
                        font-size: 20px;
                    }
                }
            }
        }
        .attr-delete-status{
            >.label_text{
                text-decoration: line-through;
                opacity: 0.5;
            }
            .idm-ant-collapse-item{
                >.idm-ant-collapse-header{
                    text-decoration: line-through;
                    opacity: 0.5;
                }
            }
        }
        .layoutType-inline{
            display: flex;
            justify-content: start;
            .label_text{
                flex-shrink: 0;
                width: 85px;
                padding-right: 5px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                line-height: 22px;
            }
            .ctrl_box{
                width: 0;
                flex-grow: 1;
            }
        }
        .label_text{
            .help_tip_icon{
                margin-left: 3px;
            }
        }
        
        .layoutType-block{
            text-align: left;
            .label_text{
                margin-bottom: 5px;
            }
            .ctrl_box{
                width: 100%;
            }
        }
        .inlineGroup{
            display: flex;
            justify-content: start;
            .layoutType-inline{
                display: flex;
                .label_text{
                    width: auto;
                    padding-right: 5px;
                    white-space: nowrap;
                    text-overflow: initial;
                    overflow: initial;
                }
                .ctrl_box{
                    width: auto;
                }
            }
            >div{
                margin-right: 10px;
                &:last-child{
                    margin-right: 0px;
                }
            }
        }
        .ctrl_box{
            .layout-icon{
                font-size: 18px;
            }
            .idm-mt5{
                margin-top: 5px;
            }
            &.uploadimg_box{
                .idm-ant-input-affix-wrapper .idm-ant-input-suffix{
                    right: 8px;
                }
            }
            
            .idm-svg-icon{
                font-size: 14px;
                max-height: 14px;
                width: 14px;
                fill: currentColor;
                vertical-align: middle;
                margin-top: -2px;
                margin-right: 5px;
            }
            .action_item{
                display: flex;
                justify-content: space-between;
                height: 30px;
                line-height: 30px;
                padding: 0px 10px;
                .action_item_name{
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                .handle{
                    cursor: ns-resize;
                }
                .action_icon{
                    display: flex;
                    &>*{
                        margin-left: 5px;
                        cursor: pointer;
                    }
                }
            }
            &.idm-layout-flex{
