                left: 32px;
                right: 32px;
            }
            .ibox_layout_handle{
                position: absolute;
                display: block;
                font-size: 0;
                border: 14px solid transparent;
                box-sizing: border-box;
                opacity: .8;
                &.ibox_handle_top,&.ibox_handle_bottom{
                    height: 0;
                    left: 2px;
                    right: 2px;
                    cursor: row-resize;
                }
                &.ibox_handle_top{
                    top: 0;
                    border-top-color: rgba(0,0,0,.3);
                    border-bottom-width: 0;
                    .box-arrow-icon{
                        bottom: 1px;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
                &.ibox_handle_bottom{
                    bottom: 0;
                    border-bottom-color: rgba(0,0,0,.3);
                    border-top-width: 0;
                    .ibox_handle_inscription{
                        position: absolute;
                        left: 3px;
                        top: -1px;
                        font-size: 12px;
                        text-transform: uppercase;
                        transform-origin: left;
                        transform: scale(.6);
                        pointer-events: none;
                    }
                    .box-arrow-icon{
                        top: 1px;
                        left: 50%;
                        transform: translateX(-50%) rotate(180deg);
                    }
                }
                &.ibox_handle_left,&.ibox_handle_right{
                    width: 0;
                    bottom: 2px;
                    top: 2px;
                    cursor: col-resize;
                }
                &.ibox_handle_left{
                    left: 0;
                    border-left-color: rgba(0,0,0,.3);
                    border-right-width: 0;
                    .box-arrow-icon{
                        right: 1px;
                        top: 50%;
                        transform: translateY(-50%) rotate(-90deg);
                    }
                }
                &.ibox_handle_right{
                    right: 0;
                    border-right-color: rgba(0,0,0,.3);
                    border-left-width: 0;
                    .box-arrow-icon{
                        left: 1px;
                        top: 50%;
                        transform: translateY(-50%) rotate(90deg)
                    }
                }
                .box-arrow-icon{
                    font-size: 11px;
                    position: absolute;
                    opacity: .6;
                    max-width: none;
                    max-height: none;
                    pointer-events: none;
                }
            }
            .ibox_inline_input{
                position: absolute;
                width: 36px;
                height: 16px;
                &.ibox_layout_input_top,&.ibox_layout_input_bottom{
                    left: 50%;
                    margin-left: -18px;
                    text-align: center;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: center;
                    }
                }
                &.ibox_layout_input_top{
                    top: -18px;
                }
                &.ibox_layout_input_bottom{
                    bottom: -18px;
                }
                &.ibox_layout_input_left,&.ibox_layout_input_right{
                    top: 50%;
                    margin-top: -8px;
                    text-align: center;
                }
                &.ibox_layout_input_left{
                    left: -38px;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: right;
                    }
                }
                &.ibox_layout_input_right{
                    right: -38px;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: left;
                    }
                }
                .ibox_inline_input_text{
                    display: block;
                    color: rgba(0,0,0,.6);
                    font-size: 12px;
                    line-height: 16px;
                    opacity: .6;
                    cursor: text;
                }
                .ibox_inline_input_input{
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    color: rgba(0,0,0,.6);
                    font-size: 12px;
                    border: none;
                    background: rgba(0,0,0,.5);
                    border-radius: 3px;
