.idm-dev-gradient-panel{
    display: flex;
    justify-items: flex-start;
    >div{
        width: 50%;
    }
    .idm-dev-gradient-panel-control{
        padding: 20px;
    }
    .idm-dev-gradient-panel-options{
        display: flex;
        >*{
            width: 50%;
        }
        margin-top: 20px;
    }
    .idm-dev-gp-title{
        padding: 10px 0;
        font-size: 16px;
        font-weight: bold;
        color: #999999;
    }
    .colors-slider {
        width: 100%;
        height: 40px;
        margin-top: 10px;
        position: relative;
        grid-column: 1 / 6;
        grid-row: 1;

        &::after {
            position: absolute;
            bottom: -20px;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: transparentize(gray, 0.7);
            content: "";
        }

        .gradient-bar {
            width: 100%;
            border-radius: 5px;
            height: 100%;
            box-shadow: 0 0 0 2px #2b2b2b;
            border: 2px solid white;
            cursor: copy;
            z-index: 2;

            &::before {
                @include checkered-background(1em);
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: inherit;
                z-index: -1;
            }
        }

        .color-stop {
            position: absolute;
            top: 0;
            height: 100%;
            width: 10px;
            z-index: 1;
            transform: translateX(-25%);
            transition: box-shadow 0.2s;

            &.stackTop {
                z-index: 2;
            }

            &.hide {
                display: none;
            }

            &.selected {
                z-index: 2;

                .color-view {
                    box-shadow: 0 0 0 2px #2b2b2b, 0 0 0px 5px rgba(189, 195, 199, 0.71);
                }

                .stop-input input {
                    box-shadow: inset 0 0 0 2px #bdc3c7;
                    opacity: 1;
                    font-size: 1em;
                    transform: translate(0, 15%);

                }
            }

            .color-view {
                width: 20px;
                height: 20px;
                position: absolute;
                bottom: 100%;
                left: 0;
                display: flex;
                justify-content: center;
                align-content: center;
                border-radius: 5px;
                cursor: move;
                border: 2px solid white;
                box-shadow: 0 0 0 2px #2b2b2b;
                transition: box-shadow 0.2s;
                border-radius: 40% 60% 100% 0% / 100% 60% 40% 0%;
                transform: rotate(-45deg) translateX(-50%);
                z-index: 1;

                &:hover {
                    box-shadow: 0 0 0 2px #2b2b2b, 0 0 0px 5px rgba(180, 180, 180, 0.6);
                }

            }

            .stop-input {
                position: absolute;
                bottom: 100%;
                left: 0%;
                transform: translate(-50%, -80%);

                input {
                    border: 0px solid gray;
                    text-align: center;
                    outline: none;
                    font-size: 0.8em;
                    box-shadow: 0 0 0 0px gray;
                    transition: all 0.2s;
                    max-width: 100%;
                    border-radius: 5px;
                    padding: 0.4em 0.4em;
                    // opacity: 0.5;
                    box-shadow: 0 0 5px rgba(0,0,0,0.2);
                    user-select: none;
                    color: #333333;

                    &:focus {}
                }
            }

        }
    }
    .colors-options-container {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        align-content: flex-start;



        .colors-options-top {
            width: 100%;
            width: 100%;
            display: grid;
            justify-items: center;
            grid-template-columns: 1fr 1fr 1fr;
        }

        .colors-options-bottom {
            justify-content: center;
            display: flex;
            width: 100%;
            padding: 1em 0;
            flex-direction: column;
            min-height: 150px;
        }
    }
    
    .control {
        display: flex;
        font-size: 0.7em;

        &.toggle {
            color: white;
            cursor: pointer;
            text-align: center;
            text-transform: uppercase;
            height: 3em;
            width: 3em;

            > div {
                display: flex;
                width: 100%;
                padding: 0.3em;
                background-color: #3498db;
                border-radius: 5px;
                transition: all 0.3s;
                opacity: 1;
                position: relative;

                &:after {
                    content: attr(data-tip);
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translate(-50%, 0%) scale(0);
                    background-color: inherit;
                    border-radius: 3px;
                    padding: 0.3em 0.5em;
                    opacity: 0;
                    transition: all 0.3s;
                    pointer-events: none;
                }

                &:hover {
                    background-color: darken(#3498db, 5%);

                    &:after {
                        transform: translate(-50%, -150%) scale(1);
                        opacity: 1;
                    }
                }

                &.off {
                    opacity: 0.5;
                }

                .icon {
                    flex-grow: 1;
                    background-repeat: no-repeat;
                    padding: 10px;
                    display: block;
                }
            }
        }

        &.range {
            padding: 0.9em 0;
            width: 100%;
            position: relative;
            display: flex;

            .title {
                width: 100%;
                text-align: left;
                text-transform: capitalize;
                display: inline-block;
                font-size: 1em;
                position: absolute;
                top: 0%;
                color: rgba(180, 180, 180, 0.5);
                text-indent: 10%;
                pointer-events: none;
                transition: all 0.2s;
                z-index: -1;
                line-height: 1;
                padding: 0;
                margin: 0;
            }

            .amount {
                width: 20%;
                display: inline-flex;
                font-size: 0.7em;
                justify-content: flex-end;
                align-items: center;
            }

            .range {
                width: 75%;
                display: inline-flex;
                align-items: center;
            }
        }

        &.color {
            $color: rgba(180, 180, 180, 0.7);

            margin-bottom: 10px;
            width: 100%;
            border: 1px solid $color;
            border-radius: 5px;
            overflow: hidden;
            padding: 3px 5px;

            > span {
                box-sizing: border-box;
                display: flex;
                justify-content: center;
                align-items: center;

            }

            .color-box {
                width: 20%;
                position: relative;


                > span {
                    position: absolute;
                    top: 10%;
                    left: 10%;
                    width: 80%;
                    height: 80%;
                    cursor: pointer;

                    &::after,
                    &::before {
                        position: absolute;
                        top: 0%;
                        right: -30%;
                        height: 100%;
                        width: 1px;
                        background-color: $color;
                        content: "";
                    }

                    &::before {
                        left: -30%;
                    }
                }
            }

            .color-input {
                flex-grow: 1;
            }

            .color-stop {
                width: 30%;

                input {
                    text-align: center;
                }
            }

            .color-delete {
                width: 10%;
                transition: all 0.2s;
                font-size: 2em;
                cursor: pointer;
                opacity: 0.5;

                &:hover:not(.disabled) {
                    opacity: 1;
                }

                &.disabled {
                    opacity: 0;
                    cursor: default;
                }
            }

            input {
                padding: 5px 10px;
                border: 0px solid gray;
                width: 100%;
                outline: 0;
            }

        }

        &.circle {
            position: relative;
            justify-content: center;

            svg {
                g circle:last-child {
                    cursor: pointer;
                }
            }

            .amount {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 1.5em;
                text-align: center;
                display: flex;
                flex-direction: column;

                input {
                    border: 2px solid transparent;
                    width: auto;
                    text-align: center;
                    font-size: 1.2em;
                    border-radius: 4px;
                    color: #333333;
                    &:focus{
                        border-color:#3498db;
                    }
                }

                span:last-child {
                    font-size: 0.7em;
                }
            }
        }

        &.switch {
            display: flex;
            margin: 1.2em 0;
            color: rgba(180, 180, 180, 0.5);
            flex-direction: column;

            input {
                display: none;

                &:checked + label {
                    background-color: #3498db;

                    .switch {
                        left: 55%;
                    }
                }
            }

            label {
                width: 30px;
                height: 15px;
                border-radius: 10px;
                background-color: gray;
                display: inline-block;
                margin-right: 10px;
                position: relative;
                transition: all 0.3s;
                cursor: pointer;

                span {
                    background-color: white;
                    height: 80%;
                    width: 40%;
                    position: absolute;
                    border-radius: 50%;
                    left: 5%;
                    top: 8%;
                    transition: all 0.3s;
                }
            }
        }
    }
    
    .color-stops-container {
        .vc-chrome {
            width: 100%;
            box-shadow: 0 0 1px 0 gray;

            .vc-chrome-saturation-wrap .vc-saturation-circle {
                transform: translate(-50%, -50%);
                box-shadow: 0 0 0 2px #fff, inset 0 0 1px 2px rgba(0, 0, 0, 0.5), 0 0 1px 1px rgba(0, 0, 0, 1);
            }
        }
    }
    .color-presets-container {
        display: flex;
        flex-direction: column;

        .user-generated {
            border-bottom: 1px solid rgba(180, 180, 180, 0.4);
            display: grid;
            width: 100%;
            grid-template-columns: 82% 18%;
            padding: 10px;
            padding-left: 0px;
            padding-right: 0px;
            padding-top: 0;

            .user-presets {
                display: grid;
                grid-template-columns: repeat(auto-fill, 35px);
                grid-gap: 10px;
                padding: 5px 0px;

                &::after {
                    content: "";
                    background-color: rgba(180, 180, 180, 0.4);
                    position: absolute;
                    right: 0;
                    top: 20%;
                    height: 80%;
                    width: 1px;
                }

                > span {
                    height: 35px;
                    cursor: pointer;
                    border-radius: 3px;
                    border: 2px solid white;
                    box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.3);
                    position: relative;
                    z-index: 1;
                    transition: all 0.2s;

                    &.selected {
                        box-shadow: 0 0 4px 3px rgba(0, 0, 0, 0.7);
                    }
                }
            }

            .presets-actions {
                display: flex;
                justify-content: space-around;
                padding: 5px 0px;

                > div {
                    filter: grayscale(1);
                    opacity: 0.2;
                    cursor: default;
                    transition: all 0.2s;
                    width: 45%;

                    $color: #39D5FF;
                    color: darken($color, 20%);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid;
                    border-radius: 2px;
                    font-size: 1.4em;
                    padding: 5px;
                    background-color: lighten($color, 10%);

                    &.save-preset {
                        $color: #2ECC71;
                        color: darken($color, 20%);
                        background-color: lighten($color, 10%);


                    }

                }

                &.enable-save .save-preset,
                &.enable-apply .apply-preset {
                    opacity: 1;
                    cursor: pointer;
                    filter: grayscale(0);

                    &:hover {
                        opacity: 0.7;
                    }
                }

            }
        }

        .other-presets {
            display: grid;
            grid-template-columns: repeat(auto-fill, 35px);
            grid-gap: 10px;
            padding: 10px;
            max-height: 143px;
            overflow: auto;
            width: 100%;
            padding-left: 1px;
            padding-right: 0px;

            > .gradient-div {
                height: 35px;
                cursor: pointer;
                border-radius: 3px;
                border: 2px solid white;
                box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.3);
                position: relative;
                z-index: 1;

                &:hover {}

            }
        }

        .more-presets {
            $color: #d35400;
            opacity: 1;
            color: darken($color, 20%);
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid $color;
            border-radius: 2px;
            transition: all 0.2s;
            font-size: 0.7em;
            padding: 5px;
            margin: 10px;
            cursor: pointer;

            &:hover {
                background-color: lighten($color, 10%);
            }
        }
    }
    
    input[type="range"] {
        $thumb: #3498db;
        $size: 10px;
        $width: 100%;
        $height: 3px;
        $track-color: #ddd;
        $track-focus: lighten($thumb, 15%);

        /* fix for FF unable to apply focus style bug  */
        border: 1px solid transparent;

        /*required for proper track sizing in FF*/
        width: 100%;

        /* Webkit */
        -webkit-appearance: none;

        &::-webkit-slider-runnable-track {
            width: $width;
            height: $height;
            background: $track-color;
            border: none;
            border-radius: 3px;
            transition: all 0.2s;
        }

        &::-webkit-slider-thumb {
            -webkit-appearance: none;
            border: none;
            height: $size;
            width: $size;
            border-radius: 50%;
            background: $thumb;
            margin-top: -4px;
        }

        &:focus {
            outline: none;

            &::-moz-range-track {
                background: $track-focus;
            }

            &::-webkit-slider-runnable-track {
                background: $track-focus;
            }

            &::-ms-fill-lower {
                background: $track-focus;
            }

            &::-ms-fill-upper {
                background: $track-focus;
            }
        }

        /* FF */
        &::-moz-range-track {
            width: $width;
            height: $height;
            background: $track-color;
            border: none;
            border-radius: 3px;
            transition: all 0.2s;
            z-index: 2;
        }

        &::-moz-range-thumb {
            border: none;
            height: $size;
            width: $size;
            border-radius: 50%;
            background: $thumb;
            z-index: 2;
        }

        /*hide the outline behind the border*/
        &:-moz-focusring {
            outline: 1px solid white;
            outline-offset: -1px;
        }

        /* IE*/
        &::-ms-track {
            width: $width;
            height: $height;

            /*remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead */
            background: transparent;

            /*leave room for the larger thumb to overflow with a transparent border */
            border-color: transparent;
            border-width: 6px 0;

            /*remove default tick marks*/
            color: transparent;
        }

        &::-ms-fill-lower {
            background: $track-color;
            border-radius: 10px;
        }

        &::-ms-fill-upper {
            background: $track-color;
            border-radius: 10px;
        }

        &::-ms-thumb {
            border: none;
            height: $size;
            width: $size;
            border-radius: 50%;
            background: $thumb;
            transform: translateY(20%);
        }
    }
}

