@import "@/style/common/common.scss";









































































































































































































































































































































































































































































































































































































































































































$sizes: 15, 16, 17, 18, 20, 21, 22, 23, 24, 25;

#idm-svg-editor-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 59;
}

#idm-svg-editor-root {
    width: 100%;
    height: 100%;
}

#idm-svg-editor-text {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    top: -10000px;
    left: -10000px;
}

.idm-svg-editor-toolbar {
    position: absolute;
    background: #fff;
    box-shadow: 0px 0px 5px rgb(0 0 0 / 20%);

    .dragger-tool {
        background: #ccc;
        height: 5px;
        width: 75%;
        border-radius: 10px;
        margin: 3px auto 0;
    }

    .toolbar-top {
        list-style: none;
        display: flex;
        align-content: center;
        flex-direction: column;
        padding: 3px;
        margin: 0;

        .toolbar-item {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            border-radius: 4px;
            list-style: none;
            text-align: center;
            width: 28px;
            height: 28px;
            padding: 10px 4px;
            margin: 3px 0;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            &.mode-selected {
                background: #F1F1F1;
            }

            &:hover {
                background: #1890ff;
                color: #fff;

                .idm-svg-editor-svg {
                    fill: #fff;
                }
            }
        }
    }

    .toolbar-bottom {
        border-top: 1px solid #eee;
        display: flex;
        align-content: center;
        flex-direction: row-reverse;
        padding: 3px 6px;
        height: 30px;
    }

    @each $c in $sizes {
        .idm-svg-editor-size-#{$c} {
            width: $c + px;
            font-size: $c + px;
        }
    }
}

#color-picker-container {
    position: absolute;
    display: none;
    background: #fff;
    height: 385px;
    border-radius: 3px;
    z-index: 5;
    box-shadow: 0 5px 10px #000;
    width: 560px;
}
